body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
    font-size: 30px;
    font-family: Consolas, Monaco, "Andale Mono", "Ubuntu Mono", monospace;
}

.outer {
    padding: 30px;
    display: -ms-flexbox;
    display: flex;
}

.outer div {
    display: inline-block;
}

.input {
    background: white;
    margin: 0 0 0 14px;
    padding: 6px 2px;
    border-radius: 5px;
    -webkit-box-shadow: inset 0 1px 5px rgba(0, 0, 0, 0.6);
            box-shadow: inset 0 1px 5px rgba(0, 0, 0, 0.6);
    -ms-flex-positive: 1;
        flex-grow: 1;
}

.input:hover {
    cursor: text;
}

.markov-display div {
    padding: 2px 10px;
    color: rgba(0, 0, 0, 0.9);
}

input {
    margin: 0 0 0 5px;
    font-size: inherit;
    border: 1px solid transparent;
    border-top: none;
    opacity: 0.01;
}

input:focus {
    outline: none;
}

.instruction {
    float: right;
    color: #aaa;
    margin: 3px 10px 0 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

.shuffle {
    font-size: 40px;
}

.shuffle:hover {
    cursor: pointer;
}

.caret {
    position: relative;
    top: 8px;
    margin-left: 4px;
    width: 2px;
    height: 30px;
    padding: 0;
    background-color: #222;
    animation: blink-animation 1s ease-out infinite;
    -webkit-animation: blink-animation 1s steps(5, start) infinite;
}

@keyframes blink-animation {
    to {
        visibility: hidden;
    }
}

@-webkit-keyframes blink-animation {
    to {
        visibility: hidden;
    }
}

@media (max-width: 800px) {
    body {
        font-size: 20px;
    }
    .shuffle {
        font-size: 30px;
    }
    .input {
        margin: 0 0 0 14px;
        padding: 4px 2px;
    }
    .outer {
        padding: 10px;
    }
    .caret {
        visibility: hidden;
        -webkit-animation: unset;
                animation: unset;
        display: none;
        top: 0;
        height: 0;
    }
    input {
        opacity: 1;
        margin-left: 0;
    }
    .instruction {
        visibility: hidden;
    }
    .markov-display div {
        padding: 2px 6px;
    }
}


